.detail-page {
  padding: 40px;
  max-width: 960px;
  margin: 0 auto;
  font-family: 'Noto Sans KR', sans-serif;
}

.top-action-bar {
  margin-bottom: 20px;
}

.back-button {
  background: none;
  border: none;
  font-size: 15px;
  cursor: pointer;
  color: #4c6ef5;
}

.detail-header {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 28px;
}

.info-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  margin-bottom: 32px;
}

.info-field {
  background: #f8f9fa;
  border-radius: 12px;
  padding: 16px;
}

.info-label {
  font-weight: 600;
  margin-bottom: 6px;
  color: #444;
}

.info-value {
  font-size: 15px;
}

.status-active {
  background-color: #d9fdd3;
  color: #2c7a1c;
  padding: 4px 10px;
  border-radius: 6px;
  display: inline-block;
}

.status-pending {
  background-color: #fff4cc;
  color: #b28800;
  padding: 4px 10px;
  border-radius: 6px;
  display: inline-block;
}

.status-inactive {
  background-color: #f2f2f2;
  color: #888;
  padding: 4px 10px;
  border-radius: 6px;
  display: inline-block;
}

.detail-actions {
  display: flex;
  gap: 12px;
  margin-bottom: 40px;
}

.detail-actions button {
  padding: 10px 20px;
  border-radius: 8px;
  font-weight: bold;
  cursor: pointer;
  border: none;
  background-color: #4c6ef5;
  color: white;
}

.detail-actions .secondary {
  background-color: #e0e0e0;
  color: #333;
}

.history-section {
  margin-top: 40px;
}

.history-title {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 16px;
}

.history-list {
  list-style: none;
  padding-left: 0;
}

.history-list li {
  margin-bottom: 10px;
  font-size: 14px;
}

.history-date {
  font-weight: bold;
  margin-right: 10px;
}

button.secondary {
  background-color: #f8f9fa;
  color: #333;
  border: 1px solid #ccc;
}

button.secondary:hover {
  background-color: #e03131;
  color: white;
  border-color: #e03131;
}