.top-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.car-thumbnail {
  width: auto;
  height: 35px;
  border-radius: 4px;
  object-fit: cover;
}

.status-chip {
  padding: 4px 10px;
  border-radius: 12px;
  font-weight: bold;
  display: inline-block;
  font-size: 0.85rem;
  text-transform: uppercase;
  color: #fff;
}

/* 상태별 스타일 (배경색만 다르게) */
.status-chip.WANTED {
  background-color: #d32f2f; /* 빨강 */
}

.status-chip.TRACKING {
  background-color: #ef6c00; /* 주황 */
}

.status-chip.CAPTURED {
  background-color: #388e3c; /* 녹색 */
}

.status-chip.INACTIVE {
  background-color: #757575; /* 회색 */
}

.status-chip.DELETED {
  background-color: #8e24aa; /* 보라 */
}


.table-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
}

.user-table td button {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 16px;
  padding: 4px;
}

/* 수배 차량 전용 커스터마이징 */
.wanted-page .table-footer {
  flex-direction: column;
  align-items: flex-start;
  gap: 12px;
}

.wanted-page .pagination-wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
}

.pagination {
  display: flex;
  gap: 6px;
}
