.edit-form-wrapper {
  max-width: 500px;
  margin: 30px auto;
  padding: 40px;
  background: #fff;
  border-radius: 16px;
  box-shadow: 0 4px 20px rgba(0,0,0,0.08);
}

.edit-form-wrapper h2 {
  font-size: 1.8rem;
  text-align: center;
  margin-bottom: 24px;
}

.form-group {
  display: flex;
  flex-direction: column;
  margin-bottom: 16px;
  font-weight: 500;
}

.form-group input,
.form-group select {
  margin-top: 6px;
  padding: 10px;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 8px;
}

.readonly-field input {
  background-color: #f9f9f9;
  color: #888;
}

.toggle-row {
  display: flex;
  align-items: center;
  gap: 10px;
  margin: 20px 0;
}

.reset-password-btn {
  background-color: #eee;
  color: #444;
  font-size: 0.85rem;
  padding: 8px 12px;
  border-radius: 6px;
  border: none;
  margin-bottom: 20px;
  cursor: pointer;
}

.button-row {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
}

.button-row button {
  padding: 10px 16px;
  font-size: 0.95rem;
  border: none;
  border-radius: 8px;
  cursor: pointer;
}

.button-row button[type="submit"] {
  background-color: #4169e1;
  color: #fff;
}

.button-row button[type="button"] {
  background-color: #eee;
  color: #333;
}
