.controls {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  margin-bottom: 12px;
}

.left-controls,
.right-controls {
  display: flex;
  gap: 8px;
  flex-shrink: 0;
}

.center-search {
  flex: 1;
  display: flex;
  justify-content: center;
}

.center-search input[type="text"] {
  width: 100%;
  max-width: 420px;
  padding: 6px 12px;
  border-radius: 6px;
  border: 1px solid #ddd;
}
.car-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 16px;
  font-size: 14px;
}

.car-table td {
  border-bottom: 1px solid #ddd;
  padding: 12px;
  text-align: center;
  vertical-align: middle;
}

.car-table th {
  background-color: #f1f3f5;
  font-weight: 600;
}

.car-table td {
  border-bottom: 1px solid #ddd;
  text-align: center;
}

.table-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
}

.table-footer button {
  padding: 8px 16px;
  border-radius: 6px;
  border: none;
  background-color: #e2e8f0;
  cursor: pointer;
}

/* 상태 칩 공통 스타일 */
.status-chip {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 6px 12px;
  border-radius: 12px;
  font-size: 13px;
  font-weight: 600;
  min-width: 64px;
  white-space: nowrap;
  color: white;
}

/* 상태별 색상 */
.status-chip.ACTIVE {
  background-color: #22c55e;
}

.status-chip.INACTIVE {
  background-color: #aaa;
}


.pagination {
  margin-top: 16px;
  display: flex;
  justify-content: center;
  gap: 6px;
}

.pagination button {
  padding: 6px 12px;
  border-radius: 6px;
  background-color: #eee;
  border: 1px solid #ccc;
  cursor: pointer;
}

.pagination button.active {
  background-color: #4c6ef5;
  color: white;
  font-weight: bold;
}

.pagination button:disabled {
  opacity: 0.4;
  cursor: not-allowed;
}

.filters {
  display: flex;
  gap: 8px;
  margin-right: 12px;
}

.filters button {
  padding: 6px 12px;
  border-radius: 6px;
  border: 1px solid #ccc;
  background-color: white;
  cursor: pointer;
}

.filters button.active {
  background-color: #4c6ef5;
  color: white;
}

.sorts {
  display: flex;
  align-items: center;
  gap: 6px;
}
