.create-form-wrapper {
  max-width: 500px;
  margin: 60px auto;
  padding: 40px;
  background: #fff;
  border-radius: 16px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
}

.create-form-wrapper h2 {
  font-size: 1.8rem;
  font-weight: 700;
  text-align: center;
  margin-bottom: 24px;
}

.create-form-wrapper form {
  display: flex;
  flex-direction: column;
}

.create-form-wrapper label {
  display: flex;
  flex-direction: column;
  font-weight: 500;
  font-size: 0.95rem;
  margin-bottom: 1.5rem;
}

.create-form-wrapper input,
.create-form-wrapper select {
  margin-top: 6px;
  padding: 10px 12px;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 8px;
  transition: border-color 0.2s ease;
}

.create-form-wrapper input:focus,
.create-form-wrapper select:focus {
  border-color: #4169e1;
  outline: none;
}

.create-form-wrapper hr {
  margin: 24px 0;
  border: none;
  border-top: 1px solid #eee;
}

.create-form-wrapper strong {
  font-size: 1.1rem;
  margin-bottom: 12px;
}

.create-form-wrapper .button-group {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  margin-top: 20px;
  flex-direction: row;
}

.create-form-wrapper .button-group button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 60px;
  padding: 10px 16px;
  border-radius: 8px;
  font-size: 0.95rem;
  cursor: pointer;
  border: none;
  transition: background-color 0.2s ease;
}

.create-form-wrapper .button-group button[type="submit"] {
  background-color: #4169e1;
  color: white;
}

.create-form-wrapper .button-group button[type="button"] {
  background-color: #eee;
  color: #333;
}

.horizontal-toggle {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 10px;
  margin-bottom: 20px;
  font-weight: 500;
}
