.user-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 16px;
  font-size: 14px;
}

.user-table th,
.user-table td {
  padding: 12px;
  border-bottom: 1px solid #e5e5e5;
  text-align: center;
}

.status-label {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 6px 12px;
  border-radius: 12px;
  font-size: 13px;
  font-weight: 600;
  min-width: 64px;
  white-space: nowrap;
  color: white;
}

.status-label.active {
  background-color: green;
}

.status-label.inactive {
  background-color: #aaa;
}

.center-search {
  flex: 1;
  display: flex;
  justify-content: center;
}

.search-input {
  flex: 1;
  max-width: 400px;
  padding: 8px 12px;
  border-radius: 8px;
  border: 1px solid #ccc;
  font-size: 14px;
}

.table-footer .pagination button {
  min-width: auto;
  padding: 6px 10px;
  font-size: 14px;
  margin: 0 4px;
  border: 1px solid #ccc;
  border-radius: 6px;
  background-color: white;
  cursor: pointer;
  white-space: nowrap;
}

.table-footer .pagination .active-page {
  background-color: #333;
  color: white;
  font-weight: bold;
}

.table-footer .delete-button {
  padding: 8px 16px;
  min-width: 100px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 6px;
  background-color: #f9f9f9;
  white-space: nowrap;
  cursor: pointer;
}
