.edit-wrapper {
  display: flex;
  align-items: flex-start;
  gap: 40px;
  margin-top: 40px;
  padding: 20px 40px;
}

.edit-wrapper img {
  width: 400px;
  border-radius: 12px;
  object-fit: cover;
  border: 1px solid #ddd;
}

.edit-wrapper form {
  flex: 1;
}

.edit-wrapper form h2 {
  margin-bottom: 20px;
}

.edit-wrapper label {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  font-weight: 500;
}

.edit-wrapper label input,
.edit-wrapper label select {
  padding: 8px 12px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 6px;
  flex: 1;
  margin-left: 16px;
  min-width: 240px;
}

.edit-wrapper .button-group {
  margin-top: 24px;
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}

.edit-wrapper .button-group button {
  padding: 8px 16px;
  font-size: 14px;
  background-color: #e2e8f0;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

.edit-wrapper .button-group button:last-child {
  background-color: #4c6ef5;
  color: white;
}
