.sidebar {
  width: 220px;
  height: 100vh;
  background-color: white;
  border-right: 1px solid #e5e5e5;
  padding: 20px;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  display: flex;
  flex-direction: column;
  box-shadow: 2px 0 8px rgba(0, 0, 0, 0.05);
}

.sidebar-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.close-btn {
  background: none;
  border: none;
  font-size: 20px;
  cursor: pointer;
}

.sidebar-logo {
  font-size: 18px;
  font-weight: bold;
}

.sidebar-menu {
  list-style: none;
  padding: 0;
  margin-top: 40px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.sidebar-menu li {
  display: flex;
  align-items: center;
  gap: 12px;
  font-size: 16px;
  color: #1a1a1a;
  cursor: pointer;
}

.sidebar-menu li:hover {
  color: #4c6ef5;
}
