.user-detail-wrapper {
  display: flex;
  padding: 40px 0;
  justify-content: center;
  align-items: center;
  font-family: "Pretendard", sans-serif;
}

.user-detail-card {
  display: flex;
  flex-direction: column;
  gap: 24px;
  padding: 32px 40px;
  background-color: white;
  border-radius: 16px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  max-width: 500px;
  width: 100%;
}

.back-button-wrapper {
  margin-bottom: 16px;
}

.user-section h2 {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
}

.user-field {
  display: flex;
  justify-content: space-between;
  margin-bottom: 12px;
  font-size: 15px;
  line-height: 1.6;
}

.user-label {
  min-width: 130px;
  padding-right: 8px;
  color: #444;
  font-weight: 600;
}

.user-value {
  flex: 1;
  color: #222;
}

.edit-button-wrapper {
  margin-top: 24px;
  display: flex;
  justify-content: flex-end;
}

.status-active {
  color: #2ecc71;
  font-weight: bold;
}

.status-inactive {
  color: #aaa;
  font-style: italic;
}
