.app-layout {
  display: flex;
  min-height: 100vh;
  overflow: hidden;
}

/* 사이드바는 왼쪽 고정 */
.app-layout > .sidebar {
  width: 220px;
  flex-shrink: 0;
}

/* 오른쪽 메인 영역 */
.main-area {
  display: flex;
  flex-direction: column;
  flex: 1;
  height: 100vh;
  overflow: hidden;
}

/* TopBar는 고정 */
.main-area > .top-bar {
  height: 60px;
  flex-shrink: 0;
  border-bottom: 1px solid #ddd;
}

/* Outlet이 들어가는 콘텐츠 영역 */
.content-area {
  flex: 1;
  overflow-y: auto;
  padding: 24px;
  min-height: 0;
}
