.top-bar-wrapper {
  display: flex;
  align-items: center;
  padding: 24px 20px;
  position: relative;
  gap: 12px;
}

.sidebar-toggle {
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  margin-right: 8px;
}

.filter-wrapper {
  position: relative;
}

.hamburger {
  font-size: 24px;
  cursor: pointer;
  padding: 4px 10px;
}

.search-bar {
  flex: 1;
  padding: 10px 16px;
  border: 1px solid #ccc;
  border-radius: 30px;
  font-size: 14px;
  background-color: white;
  min-width: 300px;
}

.profile-icon {
  font-size: 24px;
  margin-left: 16px;
}